  /**
   * @function getFieldSize
   * @name module:Template/TemplateMod.TemplateMod#getFieldSize
   * @description     .
   *
   * @param  {String} type  
   * @return  {Object} { w: , h:  }  default - { w: 150, h: 40 };
   * @Date : 2021. 5. 31
   */
  getFieldSize(type) {
    var size = { w: 150, h: 40 };
    switch (type) {
      case "TextField":
      case "NumberField":
      case "DateField":
      case "LabelField":
      case "ComboboxField":
        size = { w: TextField.widthValue(), h: TextField.heightValue() };
        break;
      case "SignField":
      case "ImageField":
        size = { w: SignField.widthValue(), h: SignField.heightValue() };
        break;
      case "MultiLineTextField":
        size = {
          w: MultiLineTextField.widthValue(),
          h: MultiLineTextField.heightValue(),
        };
        break;
      case "CheckboxField":
        size = {
          w: CheckboxField.widthValue(),
          h: CheckboxField.heightValue(),
        };
        break;
      case "RadioField":
        size = { w: RadioField.widthValue(), h: RadioField.heightValue() };
        break;
    }
    return size;
  }

